.skillsBlock {
    /*vh - видимая высота экрана*/
    height: 100vh;
    background-color: beige;

}

.skillsContainer {
    flex-direction: column;
    justify-content: flex-start;
}

.title {
    background-color: aquamarine;
}

.skills {
    border: 1px solid;
    width: 100%;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}